import { FirebaseService, Tasks } from './../shared/firebase/firebase.service';
import { Component, OnInit, Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';


@Component({
      moduleId: module.id,
      selector: 'app-story',
      templateUrl: 'prose.component.html',
      styleUrls: ['prose.component.css'],


})

@Injectable() // <<<=== required if the constructor has parameters

export class ProseComponent  implements OnInit {
  stories: Stories[];


    constructor(private _firebaseService: FirebaseService) {

    }
    ngOnInit() {
      this._firebaseService.getStories()
      .subscribe(
        stories => {this.stories = stories;
      });
    }
}
export interface Stories {
  $key?: string;
  StoryURL: string;
  title: string;
  Chapters: any[];

}



