import { FirebaseService, Tasks } from './../shared/firebase/firebase.service';
import { Component, OnInit, Injectable } from '@angular/core';
import { Location } from '@angular/common'; // required to use the Back f
import { Observable } from 'rxjs/Observable';

/**
 * This class represents the lazy loaded AboutComponent.
 */

 @Component({
  moduleId: module.id,
  selector: 'app-sd-about',
  templateUrl: 'about.component.html',
  styleUrls: ['about.component.css'],

})

@Injectable() // <<<=== required if the constructor has parameters

export class AboutComponent implements OnInit {
  tasks: Tasks[];
  appState: string;
  activeKey: string;

  constructor(
    private _firebaseService: FirebaseService
  ) {

  }

  ngOnInit() {
    this._firebaseService.getTasks()
    .subscribe(
      tasks => {this.tasks = tasks;
    });

  }

  changeState(state, key) {
    if (key) {
      this.activeKey = key;
    }
    this.appState = state;
  }

}





