import { Component, OnInit, OnDestroy  } from '@angular/core';
import { ProseService } from '../../shared/prose/prose.service';
import { Observable } from 'rxjs/Observable';
import { Observer } from 'rxjs/Observer';
import { Subscription } from 'rxjs/Subscription';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import { FirebaseService } from './../../shared/firebase/firebase.service';
import { Location } from '@angular/common'; // required to use the Back f

@Component({
      moduleId: module.id,
      selector: 'app-chapters',
      templateUrl: 'chapter.component.html',
      styleUrls: ['chapter.component.css'],

})
export class ChapterComponent implements OnInit {

    errorMessage: string;
    stories: Stories[];
    chapters: Chapters[];
    $key: any;
    subscription: Subscription;


    /**
     * Creates an instance of the ChapterComponent with the injected
     * ProseService.
     *
     * @param {ProseService} ProseService - The injected ProseService.
     */

      constructor(
          private proseService: ProseService,
          private location: Location,
          private _firebaseService: FirebaseService
        ) { }
        ngOnInit() {
          this._firebaseService.getChapters()

          .subscribe(
            chapters => {
              console.log(chapters[0]);
             this.chapters = chapters;
            });
        }
        goBack(): void {
             this.location.back();
        }
}
export interface Stories {
  $key?: string;
  StoryURL: string;
  title: string;
  Chapters: Chapters[];

}
export interface Chapters {
  $key?: string;
  Chapter: string;
  URL: string;
  description: string;
  text: string;
  title: string;
}




