import { Stories } from './../../prose/chapter/chapter.component';
import { Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { FirebaseListObservable, AngularFireDatabase } from 'angularfire2/database';
// import '../../operators';
import { Observable } from 'rxjs/Observable';
import {AngularFireModule} from 'angularfire2';
import 'rxjs/add/operator/map';


@Injectable()

export class FirebaseService {
    tasks: FirebaseListObservable<Tasks[]>;
    stories: FirebaseListObservable<Stories[]>;
    chapters: FirebaseListObservable<Chapters[]>;


        constructor(private db: AngularFireDatabase) {

        }

        getTasks() {
            this.tasks = this.db.list('/tasks') as FirebaseListObservable<Tasks[]>;
            return this.tasks;
        }
        getStories() {
            this.stories = this.db.list('/stories') as FirebaseListObservable<Stories[]>;
            return this.stories;
        }
        getChapters() {
            this.chapters = this.db.list('/stories/0') as FirebaseListObservable<Chapters[]>;
            return this.chapters;
        }
}



export interface Tasks {
    $key?: string;
    title?: string;
    status?: boolean;
    description?: string;
    SLA?: string;
    startDate?: string;
    endDate?: string;
    createdBy?: string;
}

export interface Stories {
    $key?: string;
    StoryURL: string;
    title: string;
    Chapters: any[];

}
export interface Chapters {
    $key?: string;
    Chapter: string;
    URL: string;
    description: string;
    text: string;
    title: string;
  }



